.projects {
    color: #3D405B;
    background-color: #F1F0F0;
    padding-left: 3vw;
    padding-bottom: 25vh;
    max-width: 100%;
    overflow-x: hidden;
}

.project-title {
    padding-top: 30px;
}


.project-list {
    text-align: left;
    padding-left: 3vw;
    display: grid;
    width: 100%;
    height: 300px;
    column-gap: 10vw;
    row-gap: 15vh;
    grid-template-areas: 
    "  .     .   proj3"
    "  .   proj2 proj3"
    "proj1 proj2   .  "
    "proj1   .     .  ";
    grid-template-columns: 280px 280px 280px;
}

.box {
    padding-left: 20px;
    padding-right: 20px;
}

#proj1 {
    background-color: #DDD8D0;
    grid-area: proj1;
}

#proj2 {
    background-color: #D9D9D9;
    grid-area: proj2;
}

#proj3 {
    background-color: #DBDFEE;
    grid-area: proj3;
}